#toast-container {
	position       : fixed;
	bottom         : 15px;
	right          : 15px;
	z-index        : 9999999;
	pointer-events : none;
}
#toast-container .toast {
	margin-top    : 10px;
	position         : relative;
	pointer-events   : auto;
	overflow         : hidden;
	padding          : 20px 35px 20px 20px;
	width            : 400px;
	background-color : #262626;
	color            : #ccc;
	border-radius    : 2px;
	box-shadow       : 0 1px 5px rgba(0, 0, 0, .5);
}
#toast-container .toast-dismissible .close {
	top: -16px;
	right: -26px;
	color: #fff;
	padding: 10px;
	opacity: .7;
	text-shadow: initial;
}

#toast-container .toast-dismissible .close:hover {
	opacity: 1;
}


.bs-view-loading {
	background-color : rgba(255, 255, 255, .9);
	position         : absolute;
	top              : 0; left : 0; bottom : 0; right : 0;
	z-index          : 1;
}
.bs-view-loading .loading-spinner-outer {
	position : absolute; top : 50%; left : 50%;
}
.bs-view-loading .loading-text {
	position    : absolute;
	top         : 50%;
	text-align  : center;
	font-weight : 200;
	font-size   : 2em;
	width       : 100%;
	color       : #999;
	margin-top  : 1em;
}
.bs-view-loading .spinner {
	position   : relative; margin-top : -50%; margin-left : -50%;
	/*margin: 100px auto 0;*/
	width      : 70px;
	text-align : center;
}
.bs-view-loading .spinner > div {
	width             : 18px;
	height            : 18px;
	background-color  : rgba(0, 0, 0, .15);
	border-radius     : 100%;
	display           : inline-block;
	-webkit-animation : sk-bouncedelay 1.4s infinite ease-in-out both;
	animation         : sk-bouncedelay 1.4s infinite ease-in-out both;
}
.bs-view-loading .spinner .bounce1 {
	-webkit-animation-delay : -0.32s;
	animation-delay         : -0.32s;
}
.bs-view-loading .spinner .bounce2 {
	-webkit-animation-delay : -0.16s;
	animation-delay         : -0.16s;
}
@-webkit-keyframes sk-bouncedelay {
	0%, 80%, 100% { -webkit-transform : scale(0) }
	40% { -webkit-transform : scale(1.0) }
}
@keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform : scale(0);
		transform         : scale(0);
	}
	40% {
		-webkit-transform : scale(1.0);
		transform         : scale(1.0);
	}
}

.bs-view-modal .modal-content-wrap {
	position: relative;
	z-index: 1;
}

.bs-view-modal .modal-header.movable:hover {
	cursor: move;
}

.bs-view-modal .modal-icon-wrap {
	overflow: hidden;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}

.bs-view-modal .modal-icon {
	position: absolute;
	top       : 0;
	right     : 15px;
	font-size : 20em;
	opacity   : .1;
}

.bs-view-modal .view-content {
	position: relative;
	z-index: 1;
}

.bs-view-modal .close {
	padding: 2px 5px;
}

.bs-view-modal .modal-body.no-padding {
	padding: 0
}

.bs-view-modal .media {
	padding-bottom: 15px
}

/* Modal MAX flex */
.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog.modal-max {
	width: auto;
	position: absolute;
	top: 30px;
	bottom: 30px;
	left: 30px;
	right: 30px;
	margin: 0;
}

.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog.modal-max .modal-content {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog.modal-max .modal-body {
	overflow: auto;
	flex: 1
}

.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog.modal-max .modal-content-wrap,
.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog.modal-max .view-content {
	height: 100%;
	display: flex;
	flex-direction: column;
}

@media screen and (max-width: 768px) {
	.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog,
	.m_flexbox.m_flexwrap .bs-view-modal .modal-dialog.modal-max {
		margin: 0;
		width: auto;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
	}

	.m_flexbox.m_flexwrap .bs-view-modal .modal-content {
		border-radius: 0;
	}
}

.bs-view-modal-alert .modal-body {
	padding : 0;
}
.bs-view-modal-alert .alert {
	padding       : 45px;
	margin-bottom : 0;
	border-radius : 0;
	border        : none;
}
